.servicesWrapper
{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-bottom: 8rem;
    margin-top: 9rem;
    margin-bottom: 13rem;
}

.servicesLeft
{
    display: flex;
    flex-direction: column;
    position: relative;
}

.servicesLeft >:nth-child(1)
{
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}

.servicesLeft >:nth-child(2)
{
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}

.servicesLeft >:nth-child(3)
{
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
}

.servicesButton
{
    width: 9rem;
    white-space: nowrap;
    height: 2rem;
    margin-top: 1.5rem;
}

.servicesRight
{
    position: relative;
}
.servicesRight>*
{
    position: absolute;
}

.servicesBlur
{
    top:13rem;
    left:-18rem;
}

.servicesBlur2
{
    top: 8rem;
    left: 8rem;
}