.card
{
    width: 10rem;
    height: 13rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    width: 10rem;
    text-align: center;
    background: rgba(255 255 255 0.26);
    border: 2px solid var(--orangeC);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 26px 2rem 26px;
}

.card span:nth-of-type(2)
{
    color: var(--gray);
    font-size: 16px;
}

.card > img
{
    transform: scale(0.6);
    margin-bottom: -2rem;
}

.cardButton
{
    background: white;
    box-shadow: 0px 19px 60px rgba(0 0 0 0.08);
    border-radius: 7px;
    border: 1px solid #7ADFFB;
    padding: 8px;
    color: #4877c9;
    font-size: 18px;
    transition: all .5s;
}

.cardButton:hover
{
    transform: scale(1.2);
}