.floatingDiv
{
    justify-content: space-around;
    display: flex;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding: 0px 28px 0px 0px;
    height: 4.5rem;
    opacity: 0.8 ;
}

.floatingDiv>img
{
    transform: scale(0.6);
}

.floatingDiv>span
{
    font-size: 16px;
}